import React, { Component } from 'react';
import PropTypes from 'prop-types';
import base from '../base';

import AddFishForm from './AddFishForm';

class Inventory extends Component {
	static propTypes = {
		loadSamples: PropTypes.func.isRequired,
		fishes: PropTypes.object.isRequired,
		addFish: PropTypes.func.isRequired,
		updateFish: PropTypes.func.isRequired,
		removeFish: PropTypes.func.isRequired,
		storeId: PropTypes.string.isRequired,
	};

	state = {
		uid: null,
		owner: null,
	};

	componentDidMount() {
		base.onAuth(user => {
			if (user) {
				this.authHandler(null, { user });
			}
		});
	}

	handleChange = (e, key) => {
		const fish = this.props.fishes[key];
		// take a copy of fish and update it with a new data
		const updatedFish = {
			...fish,
			[e.target.name]: e.target.value,
		};
		this.props.updateFish(key, updatedFish);
	};

	authenticate = provider => {
		console.log(`Trying to login with ${provider}`);
		base.authWithOAuthPopup(provider, this.authHandler);
	};

	logout = () => {
		base.unauth();
		this.setState({
			uid: null,
		});
	};

	authHandler = (err, authData) => {
		console.log(authData);
		if (err) {
			console.error(err);
			return;
		}
		// grab store info
		const storeRef = base.database().ref(this.props.storeId);

		// query the firsbase once for the store data
		storeRef.once('value', snapshot => {
			const data = snapshot.val() || {};

			// claim as own if no owner already
			if (!data.owner) {
				storeRef.set({
					owner: authData.user.uid,
				});
			}
			this.setState({
				uid: authData.user.uid,
				owner: data.owner || authData.user.uid,
			});
		});
	};

	renderLogin = () => {
		return (
			<nav className="login">
				<h2>Inventory</h2>
				<p>Sign in to manage your store's inventory</p>
				<button className="github" onClick={() => this.authenticate('github')}>
					Login In with Github
				</button>
				<button className="facebook" onClick={() => this.authenticate('facebook')}>
					Login In with Facebook
				</button>
				<button className="twitter" onClick={() => this.authenticate('twitter')}>
					Login In with Twitter
				</button>
			</nav>
		);
	};

	renderInventory = key => {
		const fish = this.props.fishes[key];

		return (
			<div className="fish-edit" key={key}>
				<input
					type="text"
					name="name"
					value={fish.name}
					placeholder="Fish Name"
					onChange={e => this.handleChange(e, key)}
				/>
				<input
					type="text"
					name="price"
					value={fish.price}
					placeholder="Fish Price"
					onChange={e => this.handleChange(e, key)}
				/>
				<select
					type="text"
					name="status"
					value={fish.status}
					placeholder="Fish Status"
					onChange={e => this.handleChange(e, key)}
				>
					<option value="available">Fresh!</option>
					<option value="unavailable">Sold Out!</option>
				</select>
				<input
					type="text"
					name="desc"
					value={fish.desc}
					placeholder="Fish Desc"
					onChange={e => this.handleChange(e, key)}
				/>
				<input
					type="text"
					name="image"
					value={fish.image}
					placeholder="Fish Image"
					onChange={e => this.handleChange(e, key)}
				/>
				<button onClick={() => this.props.removeFish(key)}>Remove Fish</button>
			</div>
		);
	};

	render() {
		const logout = <button onClick={this.logout}>Log Out!</button>;

		// Check if they are not logged in
		if (!this.state.uid) {
			return <div>{this.renderLogin()}</div>;
		}

		// Check if they are owner of the current store
		if (this.state.uid !== this.state.owner) {
			return (
				<div>
					<p>Sorry you aren't the owner of the store!</p>
					{logout}
				</div>
			);
		}

		return (
			<div>
				<h2>Inventory</h2>
				{logout}
				{Object.keys(this.props.fishes).map(this.renderInventory)}
				<AddFishForm addFish={this.props.addFish} />
				<button onClick={this.props.loadSamples}>Load Sample Fishes</button>
			</div>
		);
	}
}

export default Inventory;



